export const scores = Object.assign(
  {},
  ...Object.values(scoreAreas).map(element => element.components),
);

export const scoreKeys = Object.keys(scores);

export function Trs() {
  this.a = {
    lab: '',
    diagnosis: '',
    date: '',
    dateofbirth: '',
    dateofaccident: '',
    phase: '',
  };
  scoreKeys.forEach((element) => {
    if (clinicalSelects[element].multiple) {
      this.a[element] = [];
    } else {
      this.a[element] = 0;
    }
  });
}

export function cloneTrs(sample) {
  const clone = new Trs();
  clone.a = { ...sample.a };
  return clone;
}

export function subScore(scoreKey, values) {
  let onlyD = true;
  const firstNonD = { crit: null, key: null };
  const nonDDifferences = { C1: 35, C2: 35, AB: 30 };
  const nonDRanks = { C1: 2, C2: 1, AB: 0 };

  scoreKeys.forEach((key) => {
    const index = values[key];
    const { crit } = scores[key][index];
    if (crit !== 'D') {
      if (!firstNonD.crit || nonDRanks[firstNonD.crit] < nonDRanks[crit]) {
        firstNonD.crit = crit;
        firstNonD.key = key;
      }
      onlyD = false;
    }
  });

  const mapping = scores[scoreKey];
  let { score } = mapping[values[scoreKey]];
  if (!onlyD && firstNonD.key === scoreKey) {
    score += nonDDifferences[firstNonD.crit];
  }
  return score;
}

export function elementScore(key, value) {
  const mapping = scores[key];
  return mapping[value].score;
}

export function getScoreArea(key) {
  let needle = '';
  Object.entries(scoreAreas).forEach(([areaKey, area]) => {
    if (key in area.components) {
      needle = areaKey;
    }
  });
  return needle;
}

export function calculateScoreOld(trs) {
  const reducer = (accumulator, currentValue) => (
    accumulator + elementScore(currentValue, trs.a[currentValue])
  );
  return 100 - scoreKeys.reduce(reducer, 0);
}

export function calculateScore(trs) {
  let score = 0;
  scoreKeys.forEach((key) => {
    if (Object.prototype.hasOwnProperty.call(trs.a, key)) {
      score += subScore(key, trs.a);
    } else {
      trs.a[key] = 0;
    }
  });
  return 100 - score;
}
